<template>
  <div class="modal-document">
    <h4 class="fw-bold" >Zwycięski wódz</h4>

    <p class="fw-bold">Gen. Władysław Anders, dowódca Armii Polskiej w ZSRR oraz 2 Korpusu Polskiego, zasłynął jako dowódca, którego
    żołnierze zdobyli w maju 1944 roku włoskie wzgórze Monte Cassino. Generał cieszył się wielkim autorytetem wśród
    swoich podkomendnych, był dla nich nie tylko dowódcą. Traktowali go jak ojca, któremu ufali.</p>
    <p>
    „Gen. Władysław Anders był zdolnym żołnierzem, świetnym kawalerzystą i uzdolnionym dowódcą, którego kochali
    podkomendni, a także politykiem, który nie miał złudzeń co do powojennej komunistycznej władzy w Polsce” – mówi dr
    Piotr Jagodziński, historyk zajmujący się dziejami Polskich Sił Zbrojnych na Zachodzie.
    Władysław Anders urodził się 11 sierpnia 1892 roku w Błoniu koło Kutna, w rodzinie ziemiańskiej. Po maturze trafił
    do armii rosyjskiej. Ukończył szkołę podoficerską w stopniu chorążego rezerwy i rozpoczął studia politechniczne w
    Rydze. W czasie I wojny światowej został powołany do carskiej armii, w której dowodził szwadronem jazdy. Potem
    ukończył kurs Akademii Sztabu Generalnego w Petersburgu i został wysłany na front do Rumunii jako szef sztabu jednej
      z dywizji piechoty.
    </p>
<h4 class="fw-bold">Dla niepodległej</h4>
    <p>„Cały czas Anders myślał o walce dla Polski, dlatego po rewolucji lutowej w lipcu 1917 roku wstąpił do tworzonego na
    terenie Rosji I Korpusu Polskiego dowodzonego przez gen. Józefa Dowbora-Muśnickiego” – mówi historyk. Po rozbrojeniu
    Korpusu przez Niemców w maju 1918 roku przedostał się do kraju i zgłosił do polskiego wojska.
    </p>
    <p>Brał udział w powstaniu wielkopolskim jako szef sztabu Armii Wielkopolskiej, a podczas wojny polsko-bolszewickiej
    dowodził 15 Pułkiem Ułanów Poznańskich. Dał dowody swych talentów wojskowych, m.in. w starciach nad Berezyną. Za
    męstwo w walce z bolszewikami został odznaczony przez marszałka Józefa Piłsudskiego orderem Virtuti Militari V
      klasy.
    </p>
    <p>Po wojnie młody oficer studiował w słynnej paryskiej Wyższej Szkole Wojennej, a po powrocie kierował kursem dla
    wyższych dowódców w Wyższej Szkole Wojennej w Warszawie i był komendantem garnizonu w Warszawie. W trakcie zamachu
    majowego płk Anders opowiedział się po stronie rządu i zorganizował ewakuację prezydenta Stanisława Wojciechowskiego
    z Belwederu. – Stanięcie po przeciwnej stronie niż marszałek Piłsudski nie zahamowało jednak jego dalszej wojskowej
      kariery – podkreśla badacz.
    </p>
    <p>Szczególne uznanie władz wojskowych pułkownik zyskał po wielkich manewrach kawaleryjskich na Wołyniu. „Anders jest
    oficerem, przed którym w przyszłości powinny stać otworem wszystkie, nawet najwyższe, stanowiska w wojsku”,
    stwierdził po ćwiczeniach gen. Gustaw Orlicz-Dreszer, dowódca 2 Dywizji Kawalerii. Niedługo potem płk Anders został
    szefem sztabu w Generalnym Inspektoracie Jazdy. Potem dowodził Brygadą Kawalerii w Brodach, a w 1937 roku, już jako
      generał brygady, objął dowództwo Nowogródzkiej Brygady Kawalerii w Baranowiczach.
    </p>
    <p>„Gen. Anders był nie tylko świetnym kawalerzystą, ale także jeźdźcem” – dodaje dr Jagodziński. W 1932 roku
    przewodził polskiej ekipie, która podczas zawodów hippicznych w Nicei zdobyła cztery pierwsze nagrody, w tym
    najważniejszą – Puchar Narodów.
    </p>
<h4 class="fw-bold">Z nieludzkiej ziemi</h4>
    <p>Po wybuchu II wojny generał na czele Nowogródzkiej Brygady Kawalerii, a potem Grupy Operacyjnej Kawalerii walczył z
    Niemcami, m.in. w okolicach Płocka, Mińska Mazowieckiego i Tomaszowa Lubelskiego. Po napaści na Polskę 17 września
    Armii Czerwonej, gen. Anders podzielił oddziały na mniejsze grupy i polecił im przebijać się na Węgry. Sam 29
    września został ranny i w okolicach Sambora koło Lwowa dostał się do sowieckiej niewoli.
    </p>
   <p>Był przetrzymywany m.in. w centralnym więzieniu NKWD na Łubiance w Moskwie. „Sowieci wielokrotnie go przesłuchiwali
    i bezskutecznie nakłaniali do wstąpienia do Armii Czerwonej, jednak generał zdecydowanie odrzucał te propozycję” –
    zaznacza historyk.
   </p>
    <p>Sytuację Andersa zmienił wybuch wojny sowiecko-niemieckiej w czerwcu 1941 roku i podpisanie 30 lipca układu
    Sikorski-Majski, który przewidywał uwolnienie Polaków z sowieckich łagrów oraz utworzenie polskiego wojska na
    terenie Związku Radzieckiego. Na początku sierpnia generał został zwolniony z więzienia i objął dowództwo nad
    powstającą armią polską.
    </p>
    <p>Do punktów werbunkowych zgłaszały się tysiące żołnierzy i cywili. Generał starał się o zapewnienie im broni,
    umundurowania i żywności. Prowadził również rozmowy z władzami radzieckimi na temat zaginięcia polskich oficerów
    aresztowanych w 1939 roku przez Sowietów. Ich los poznał dopiero w kwietniu 1943 roku, po odkryciu przez Niemców w
    Katyniu grobów Polaków.
    </p>
    <p>W związku z kłopotami organizacyjnymi i zaopatrzeniowymi, generał naciskał na ewakuację Armii Polskiej do
    kontrolowanego przez Brytyjczyków Iranu. „Dzięki jego staraniom w marcu i sierpniu 1942 roku ze Związku Sowieckiego
    zostało ewakuowanych ponad 115 tys. polskich obywateli, w tym ok. 70 tys. żołnierzy” – wylicza dr Jagodziński.
    </p>
    <p>W Iranie oddziały zostały przemianowane na Armię Polską na Wschodzie. Polacy odbyli przeszkolenie w Iraku i
    Palestynie, a w lipcu 1943 roku, po połączeniu z Samodzielną Brygadą Strzelców Karpackich, powstał liczący ok. 50
    tys. żołnierzy 2 Korpus Polski, na którego czele stanął gen. Anders. „Generał cieszył się wielkim autorytetem wśród
    swoich żołnierzy, był dla nich nie tylko dowódcą, lecz również ojcem, któremu ufali” – podkreśla historyk.
    </p>
<h4 class="fw-bold">Walka o wzgórze</h4>
    <p>Na przełomie 1943 i 1944 roku jednostki polskie zostały przetransportowane do Włoch i podporządkowane brytyjskiej 8
    Armii. Podczas kampanii włoskiej 2 KP wsławił się przede wszystkim zdobyciem silnie ufortyfikowanego wzgórza Monte
    Cassino zamykającego drogę do Rzymu.
    </p>
    <p>Kiedy gen. Oliver Leese, dowódca 8 Armii zaproponował gen. Andersowi zdobywanie przez Polaków wzgórza, polski
    dowódca zdawał sobie sprawę, że będzie to trudne zadanie, ale przyczyni się do wzmocnienia pozycji Polski wśród
    aliantów. „Wykonanie tego zadania […] byłoby najlepszą odpowiedzią na propagandę sowiecką, która twierdziła, że
    Polacy nie chcą się bić z Niemcami. Podtrzymałoby na duchu opór walczącego Kraju. Przyniosłoby dużą chwałę orężowi
    polskiemu”, zapisał gen. Anders w książce Bez ostatniego rozdziału.
    </p>
    <p>Po tygodniu ciężkich walk 18 maja 1944 roku Polacy zdobyli Monte Cassino. Potem żołnierze 2 Korpusu pod wodzą
    Andersa zdobyli także Ankonę, przełamali linię Gotów i zdobyli Bolonię. Niestety, wkład Polaków w walkę z Niemcami
    nie wpłynął na decyzje aliantów podjęte w lutym 1945 roku na konferencji w Jałcie i pozostawienie Polski w
    sowieckiej strefie wpływów.
    </p>
    <p>„Melduję, że II Korpus nie może uznać jednostronnej decyzji, oddającej Polskę i naród na łup bolszewikom. Zwróciłem
    się do władz sojuszniczych o wycofanie korpusu z odcinków bojowych, bo nie mam sumienia żądać w obecnej chwili od
    żołnierza ofiary krwi”, napisał gen. Anders w telegramie do prezydenta Władysława Raczkiewicza dzień po ogłoszeniu
    postanowień wielkiej trójki. Alianci odmówili jednak wycofania żołnierzy polskich z frontu, tłumacząc się względami
    operacyjnymi.
    </p>
    <p>Po zakończeniu wojny generał nadal troszczył się o swoich żołnierzy. Organizował dla nich warsztaty pracy, szkoły,
    kursy zawodowe i wyższe studia na włoskich uczelniach, aby ułatwić im start w cywilnym życiu. – Generał nie miał
    żadnych złudzeń co do rzeczywistych zamiarów władzy ludowej i postanowił pozostać na emigracji w Londynie – tłumaczy
    dr Jagodziński. Zresztą już w 1946 roku władze komunistyczne pozbawiły go obywatelstwa polskiego, m.in. za to, że
    nie podporządkował się dowództwu ludowego wojska polskiego. Dopiero w 1989 roku rząd PRL uchylił tę uchwałę.
    </p>
    <p>Na emigracji gen. Anders angażował się w działalność polityczną, był m.in. przewodniczącym Skarbu Narodowego, który
    miał zasilać działania niepodległościowe, a w latach 1946-1954 Naczelnym Wodzem i Generalnym Inspektorem Polskich
    Sił Zbrojnych. „Dla emigracji niepodległościowej Anders był niekwestionowanym autorytetem. Był bohaterem walk we
    Włoszech i człowiekiem, który uratował tysiące Polaków z sowieckich łagrów, a także symbolem bezkompromisowego oporu
    wobec nowej władzy w Polsce” – mówi historyk.
    </p>
    <p>Generał zmarł 12 maja 1970 roku, w 26. rocznicę rozpoczęcia polskiej bitwy pod Monte Cassino. Zgodnie z jego wolą
    został pochowany wśród swoich żołnierzy na Polskim Cmentarzu Wojennym na Monte Cassino. W 1995 roku Władysław Anders
    został pośmiertnie odznaczony Orderem Orła Białego, najwyższym polskim odznaczeniem.
    </p>
